import React, { useState } from 'react';
import { motion } from 'framer-motion';

interface CampaignFormProps {
  onSubmit: (data: CampaignData) => void;
}

interface CampaignData {
  name: string;
  description: string;
  startDate: string;
  endDate: string;
  goalAmount: number;
  image: File | null;
  category: string;
}

const CampaignForm: React.FC<CampaignFormProps> = ({ onSubmit }) => {
  const [formData, setFormData] = useState<CampaignData>({
    name: '',
    description: '',
    startDate: '',
    endDate: '',
    goalAmount: 0,
    image: null,
    category: '',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files ? e.target.files[0] : null;
    setFormData({
      ...formData,
      image: file,
    });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData); // Pass data to the parent component
  };

  return (
    <motion.form
      className="space-y-6 bg-white p-8 rounded-lg shadow-lg w-full md:w-2/3 lg:w-1/2 mx-auto"
      onSubmit={handleSubmit}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.8 }}
    >
      <h2 className="text-2xl font-semibold text-center text-[#0b221d]">Create a Campaign</h2>

      <div>
        <label htmlFor="name" className="block text-lg font-medium text-[#0b221d]">
          Campaign Name
        </label>
        <input
          type="text"
          id="name"
          name="name"
          value={formData.name}
          onChange={handleChange}
          className="mt-2 block w-full px-4 py-2 border border-gray-300 rounded-md"
          required
        />
      </div>

      <div>
        <label htmlFor="description" className="block text-lg font-medium text-[#0b221d]">
          Description
        </label>
        <textarea
          id="description"
          name="description"
          value={formData.description}
          onChange={handleChange}
          className="mt-2 block w-full px-4 py-2 border border-gray-300 rounded-md"
          rows={4}
          required
        />
      </div>

      <div>
        <label htmlFor="startDate" className="block text-lg font-medium text-[#0b221d]">
          Start Date
        </label>
        <input
          type="date"
          id="startDate"
          name="startDate"
          value={formData.startDate}
          onChange={handleChange}
          className="mt-2 block w-full px-4 py-2 border border-gray-300 rounded-md"
          required
        />
      </div>

      <div>
        <label htmlFor="endDate" className="block text-lg font-medium text-[#0b221d]">
          End Date
        </label>
        <input
          type="date"
          id="endDate"
          name="endDate"
          value={formData.endDate}
          onChange={handleChange}
          className="mt-2 block w-full px-4 py-2 border border-gray-300 rounded-md"
          required
        />
      </div>

      <div>
        <label htmlFor="goalAmount" className="block text-lg font-medium text-[#0b221d]">
          Goal Amount ($)
        </label>
        <input
          type="number"
          id="goalAmount"
          name="goalAmount"
          value={formData.goalAmount}
          onChange={handleChange}
          className="mt-2 block w-full px-4 py-2 border border-gray-300 rounded-md"
          required
        />
      </div>

      <div>
        <label htmlFor="category" className="block text-lg font-medium text-[#0b221d]">
          Category
        </label>
        <select
          id="category"
          name="category"
          value={formData.category}
          onChange={handleChange}
          className="mt-2 block w-full px-4 py-2 border border-gray-300 rounded-md"
          required
        >
          <option value="">Select a category</option>
          <option value="education">Education</option>
          <option value="healthcare">Healthcare</option>
          <option value="environment">Environment</option>
          <option value="social">Social</option>
        </select>
      </div>

      <div>
        <label htmlFor="image" className="block text-lg font-medium text-[#0b221d]">
          Campaign Image
        </label>
        <input
          type="file"
          id="image"
          name="image"
          accept="image/*"
          onChange={handleFileChange}
          className="mt-2 block w-full px-4 py-2 border border-gray-300 rounded-md"
        />
      </div>

      <div className="text-center">
        <button
          type="submit"
          className="mt-4 px-6 py-2 bg-[#0b221d] text-white font-semibold rounded-md hover:bg-[#0a1d1b]"
        >
          Create Campaign
        </button>
      </div>
    </motion.form>
  );
};

export default CampaignForm;
